
import headTop from '@/components/header'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import Api from '@/js/common'
import { setTimeout } from 'timers'

export default {
directives: {
TransferDom
},
data() {
return {
show1: false,
menus2: [],
questionIndex: 0, //几道题
title: '',
description: '',
endTime: '', //结束时间
questionList: [], //问题列表
ques_id: '', //试卷id
submitType: 1, //提交 1 存草稿2 保存发布
endWord: '', //结束语
value1: '',
dataTitle: '截止时间',
ToastShow: false,
toastInfo: '',
oldQuesInfo: '' 
}
},
computed: {
...mapGetters(['device'])
},
methods: {
showPop() {

Api.setVal('quesAction', 'add') 
this.show1 = true
this.setData()
},
log(str1, str2 = '') {
if(str2 == 'cancel'){
this.endTime = ''
}
},
change(value) {
this.endTime = value
},
selectType(key, item) {
Api.setVal('question', item)
Api.setVal('questionId', key)
var questionIndex = parseInt(this.questionIndex) + 1
Api.setVal('questionIndex', questionIndex)
this.$router.push('/selectQuestion')
},
getquestionType() {
var vmSelf = this
vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.questionType()
.then(function(response) {
vmSelf.$vux.loading.hide()
vmSelf.menus2 = response
})
.catch(function(err) {
console.log('=====', err)
})
},
edit(index, sortId, id) {
Api.setVal('quesAction', 'edit')
Api.setVal('quesActionIndex', index)
Api.setVal('sortId',sortId)
this.setData()
if (id) {
this.$router.push({
path: '/selectQuestion',
query: { id: id }
})
} else {
this.$router.push('/selectQuestion')
}
},
del(index,sortId, id) {
var vmSelf = this
this.$vux.confirm.show({

title: '操作提示',
content: '确定删除吗？',
onCancel() {},
onConfirm() {
vmSelf.questionList.splice(index, 1)
if (id) {
var delObj = Api.getVal('delObj') ? Api.getVal('delObj') : '[]'
delObj = JSON.parse(delObj)
delObj.push(id)
delObj = JSON.stringify(delObj)
Api.setVal('delObj', delObj)
}
var questionList = JSON.stringify(vmSelf.questionList)
Api.setVal('questionList', questionList)

var editObj = Api.getVal('editObj') ? Api.getVal('editObj') : '[]'
editObj = JSON.parse(editObj)
for(var i in editObj){
if(editObj[i].sort_id == sortId){
editObj.splice(i, 1)
editObj = JSON.stringify(editObj)
Api.setVal('editObj', editObj)
}
}
var addObj = Api.getVal('addObj') ? Api.getVal('addObj') : '[]'
addObj = JSON.parse(addObj)
for(var i in addObj){
if(addObj[i].sort_id == sortId){
addObj.splice(i, 1)
addObj = JSON.stringify(addObj)
Api.setVal('addObj', addObj)
}
}

}
})
},
btnClick(index) {
var vmSelf = this
vmSelf.submitType = index
if (!vmSelf.title) {
vmSelf.alertInfo('请填写标题')
return
}

if (vmSelf.questionList.length == 0) {
vmSelf.alertInfo('请添加问题')
return
}
if (vmSelf.endWord == '') {
vmSelf.endWord = '问卷到此结束，感谢您的参与'
}
if (!vmSelf.ques_id) {
vmSelf.createQuestion()
} else {
vmSelf.editQuestion()
}
},
createQuestion() {
var vmSelf = this
var descripe = vmSelf.creatFormatCode(vmSelf.description)
var submitObj = {
ques_id: vmSelf.ques_id,
title: vmSelf.title,
descr: descripe,
questionnaire: vmSelf.endWord,
expire_date: vmSelf.endTime,
value: vmSelf.submitType,
addObj: JSON.stringify(vmSelf.questionList)
}
vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.questionCreat(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide()
vmSelf.toastInfo = response.data.data
vmSelf.ToastShow = true
setTimeout(function() {
vmSelf.$router.push('/index/creatQuestionList')
}, 3000)

})
.catch(function(err) {
vmSelf.$vux.loading.hide()
console.log('=====', err)
})
},
editQuestion() {
var vmSelf = this
var submitObj = {
ques_id: vmSelf.ques_id,
value: vmSelf.submitType,
editObj: Api.getVal('editObj'),
addObj: Api.getVal('addObj'),
delObj: Api.getVal('delObj')
}
var oldQuesInfo = vmSelf.oldQuesInfo
oldQuesInfo = JSON.parse(oldQuesInfo)
var endTime = Api.changeTimeType(oldQuesInfo.ques.expire_date)
if (
oldQuesInfo.ques.title != vmSelf.title ||
oldQuesInfo.ques.descr != vmSelf.description ||
endTime != vmSelf.endTime ||
oldQuesInfo.ques.questionnaire != vmSelf.endWord
) {
submitObj.title = vmSelf.title
submitObj.descr = vmSelf.creatFormatCode(vmSelf.description)
submitObj.questionnaire = vmSelf.endWord
submitObj.expire_date = vmSelf.endTime
}
vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.questionEdit(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide()
vmSelf.toastInfo = response.data.data
vmSelf.ToastShow = true
setTimeout(function() {
vmSelf.$router.push('/index/creatQuestionList')

}, 3000)
})
.catch(function(err) {
vmSelf.$vux.loading.hide()
console.log('=====', err)
})
},
alertInfo(title) {
var vmSelf = this
this.$vux.alert.show({
title: '提示',
content: title,
onShow() {
console.log("Plugin: I'm showing")
},
onHide() {
console.log("Plugin: I'm hiding now")
}
})
},
showPlugin() {
this.$vux.datetime.show({
cancelText: '取消',
confirmText: '确定',
format: 'YYYY-MM-DD HH',
value: '2017-05-20 18',
onConfirm(val) {
console.log('plugin confirm', val)
},
onShow() {
console.log('plugin show')
},
onHide() {
console.log('plugin hide')
}
})
},
init() {
Api.setVal('questionList', [])
Api.setVal('questionIndex', 0)
Api.setVal('editObj', '[]')
Api.setVal('addObj', '[]')
Api.setVal('delObj', '[]')
Api.setVal('oldQuesInfo', '')

this.getquestionType()
if (this.$route.query.id) {

this.ques_id = this.$route.query.id
this.getDetail()
}
},
getData() {
if (this.$route.query.id) {

this.ques_id = this.$route.query.id
}
this.title = Api.getVal('questionTitle')
? Api.getVal('questionTitle')
: ''
this.description = Api.getVal('questionDes')
? Api.getVal('questionDes')
: ''
this.endTime = Api.getVal('questionEndTime')
? Api.getVal('questionEndTime')
: ''
this.endWord = Api.getVal('questionEndWord')
? Api.getVal('questionEndWord')
: ''
this.oldQuesInfo = Api.getVal('oldQuesInfo')
? Api.getVal('oldQuesInfo')
: ''
this.questionIndex = Api.getVal('questionIndex')
? Api.getVal('questionIndex')
: 0
var questionList = Api.getVal('questionList')
this.questionList = JSON.parse(questionList)
},
setData() {
Api.setVal('questionTitle', this.title)
Api.setVal('questionDes', this.description)
Api.setVal('questionEndTime', this.endTime)
Api.setVal('questionEndWord', this.endWord)
Api.setVal('oldQuesInfo', this.oldQuesInfo)
},
previewClick(data) {

this.setData()
var quesInfo = {
title: this.title,
discription:this.creatFormatCode(this.description),
questionList: this.questionList,
endWord: this.endWord ? this.endWord : '问卷到此结束，感谢您的参与',
endTime: this.endTime ? this.endTime : '0'
}
var params = { quesInfo: quesInfo }
this.$router.push({ name: 'preview', params: params })
},
getDetail() {
let vmSelf = this
var submitObj = {
ques_id: vmSelf.$route.query.id,
ques_wj: vmSelf.$route.query.quesWj
}
vmSelf.$vux.loading.show({
text: 'Loading'
})
Api.creatDetail(submitObj)
.then(function(response) {
alert(JSON.stringify(response))
vmSelf.$vux.loading.hide()
vmSelf.oldQuesInfo = JSON.stringify(response)
vmSelf.title = response.ques.title
vmSelf.description = vmSelf.getFormatCode(response.ques.descr)
vmSelf.endWord = response.ques.questionnaire
if(response.ques.expire_date != 0){
vmSelf.endTime = Api.changeTimeType(response.ques.expire_date)
}
vmSelf.questionList = response.ques_bank
var num = 0
for(var i in response.ques_bank){
if(response.ques_bank[i].sort_id > num){
num = response.ques_bank[i].sort_id
}
}
vmSelf.questionIndex = num
var questionList = JSON.stringify(response.ques_bank)
Api.setVal('questionList', questionList)
})
.catch(function(err) {
console.log('=====', err)
})
},
creatFormatCode(strValue){
return strValue.replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, '&nbsp;');
},
getFormatCode(strValue){
return strValue.replace(/<br\/>/g, "\n").replace(/&nbsp;/g," ");
}
},
beforeRouteEnter(to, from, next) {
next(vm => {
if (from.name === 'preview' || from.name === 'selectQuestion') {
vm.getquestionType()
vm.getData()
} else {
vm.init()
}
})
},
mounted() {
appcan.setNavigationBarTitle({
title: "创建问卷"
});
},
components: {
XTextarea,
headTop,
XInput,
Group,
Actionsheet,
XHeader,
ViewBox,
Cell,
TransferDom,
Popup,
Datetime,
Toast,
XButton
}
}



